import APP from "./app";
import React from "react";
import classNames from "classnames";
import LinkedStateMixin from "react-addons-linked-state-mixin";

export const Layout = {};

Layout.Global = React.createClass({
	render: function () {
		return (
			<div className="container">
				{this.props.children}
			</div>
		);
	}
});

Layout.Navbar = React.createClass({
	onLogoff: function (event) {
		event.preventDefault();
		APP.logged = false;
		APP.render();
	},
	navigateTo(view, event) {
		event.preventDefault();
		APP.render(view);
	},
	active: function (view) {
		return APP.view === view ? "active" : "";
	},
	render: function () {
		const showNavigation = APP.role !== "Individu";
	
		return (
			<nav className="navbar navbar-default">
				<div className="container-fluid">
					<div className="navbar-header">
						{ showNavigation ?
							<button type="button" className="navbar-toggle collapsed" data-toggle="collapse" data-target="#bs-example-navbar-collapse-1" aria-expanded="false">
								<span className="sr-only">Toggle navigation</span>
								<span className="icon-bar"></span>
								<span className="icon-bar"></span>
								<span className="icon-bar"></span>
							</button>
							: null }
						<a className="navbar-brand" href="#" onClick={this.navigateTo.bind(this, APP.role === "Agent" ? "dashboard" : "login")}>Application Mtiers</a>
					</div>
					{ showNavigation ?
						<div className="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
							<ul className="nav navbar-nav">
								<li className={this.active("stagiaires")}><a href="#" onClick={this.navigateTo.bind(this, "stagiaires")}>Stagiaires</a></li>
								<li className={this.active("evenements")}><a href="#" onClick={this.navigateTo.bind(this, "evenements")} className={this.active("evenements")}>Evnements</a></li>
								<li className={this.active("salles")}><a href="#" onClick={this.navigateTo.bind(this, "salles")} className={this.active("salles")}>Salles</a></li>
								<li className={this.active("formateurs")}><a href="#" onClick={this.navigateTo.bind(this, "formateurs")}>Formateurs</a></li>
							</ul>
							<ul className="nav navbar-nav navbar-right">
								<li><a href="#" onClick={this.onLogoff}><Glyph icon="off" /> Dconnexion</a></li>
							</ul>
						</div>
						: null }
				</div>
			</nav>
		);
	}
});

Layout.Row = React.createClass({
	getDefaultProps: function () {
		return {
			size: "col-xs-12",
			spacing: ""
		};
	},
	render: function () {
		const classes = {};
		classes[this.props.size] = true;
		classes["spacing" + this.props.spacing] = true;
		
		return (
			<div className={classNames(classes)}>
				{this.props.children}
			</div>
		);
	}
});

Layout.Table = React.createClass({
	propTypes: {
		cols: React.PropTypes.array.isRequired
	},
	render() {
		const headers = this.props.cols.map(col => <th key={col}>{col}</th>);
		headers.push(<th key="nbsp">&nbsp;</th>);
		 
		return (
			<table className="table table-hover">
				<thead>
					<tr>
						{headers}
					</tr>
				</thead>
				<tbody>
					{this.props.children}
				</tbody>
			</table>
		);
	}
});

export const Form = {};

Form.Text = React.createClass({
	mixins: [LinkedStateMixin],
	propTypes: {
		name: React.PropTypes.string.isRequired,
		label: React.PropTypes.string.isRequired,
		type: React.PropTypes.string,
		min: React.PropTypes.number
	},
	getdefaultProps: function () {
		return {
			type: "text"
		};
	},
	render: function () {
		const classes = classNames({
			"form-group": true,
			"col-xs-12": true,
			"col-md-4": this.props.type === "number"
		});
		
		const attrs = {};
		
		if (this.props.min) {
			attrs.min = this.props.min;
		}

		return (
			<div className="row">
				<div className={classes}>
					<label htmlFor={this.props.name}>{this.props.label}</label>
					<input type={this.props.type} id={this.props.name} name={this.props.name} valueLink={this.props.value} required className="form-control" {...attrs} />
				</div>
			</div>
		);
	}
});

Form.Select = React.createClass({
	propTypes: {
		name: React.PropTypes.string.isRequired,
		value: React.PropTypes.object.isRequired,
		label: React.PropTypes.string.isRequired,
		options: React.PropTypes.array.isRequired,
		optionMapper: React.PropTypes.func.isRequired
	},
	render() {
		const options = this.props.options
			.map(this.props.optionMapper)
			.map(option => <option key={option.value} value={option.value}>{option.label}</option>);
			
		options.unshift(<option key="--"></option>);
			
		return (
			<div className="form-group">
				<label htmlFor={this.props.name}>{this.props.label}</label>
				<select id={this.props.name} className="form-control" valueLink={this.props.value}>
					{options}
				</select>
			</div>
		);
	}
});

Form.Checkbox = React.createClass({
	propTypes: {
		label: React.PropTypes.string.isRequired,
		value: React.PropTypes.object.isRequired
	},
	render: function () {
		return (
			<div className="checkbox">
				<label>
					<input type="checkbox" checkedLink={this.props.value} /> {this.props.label}
				</label>
			</div>
		);
	}
});

Form.Submit = React.createClass({
	propTypes: {
		create: React.PropTypes.bool.isRequired
	},
	render: function () {
		const icon = this.props.create ? "plus" : "ok";
		const label = this.props.create ? "Crer" : "Sauvegarder";
		
		return (
			<div className="row">
				<div className="col-xs-12">
					<button type="submit" className="btn btn-primary"><Glyph icon={icon} /> {label}</button>
				</div>
			</div>
		);
	}
});

export const Glyph = React.createClass({
	render: function () {
		const icon = "glyphicon glyphicon-" + this.props.icon;
		return (
			<span className={icon}></span>
		);
	}
});

export const Label = React.createClass({
	propTypes: {
		type: React.PropTypes.string,
		text: React.PropTypes.string.isRequired
	},
	getDefaultProps: function () {
		return {
			type: "default"
		};
	},
	render: function () {
		const classes = {
			label: true
		};
		classes["label-" + this.props.type] = true;
		return <span className={classNames(classes)}>{this.props.text}</span>
	}
});

export default Layout;


