import $ from "jquery";
import moment from "moment";
import classNames from "classnames";
import React, { PropTypes } from "react";
import { Tabs, Tab } from "react-bootstrap/lib";
import APP from "./app";
import { Layout, Glyph, Form, Label } from "./layout";
import { LinkedFormMixin } from "./mixins";

function create () {
	return {
		eligible: false,
		evenements: []
	};
}

function createStagiaireEvenement (id) {
	return {
		evenementId: parseInt(id),
		statut: "INVITE",
		notes: ""
	}
}

const Stagiaires = {
	create
};

Stagiaires.countParticipants = function (stagiaires, evenement) {
	return stagiaires.map(s => s.evenements)
		.reduce((arr, evenements) => { return arr.concat(evenements); }, [])
		.map(e => e.evenementId)
		.filter(id => id === evenement.id)
		.length;
};

Stagiaires.View = React.createClass({
	propTypes: {
		view: PropTypes.string,
		stagiaires: PropTypes.arrayOf(PropTypes.object).isRequired,
		evenements: PropTypes.arrayOf(PropTypes.object).isRequired,
		stagiaire: PropTypes.object,
		onEdit: PropTypes.func.isRequired,
		onSave: PropTypes.func.isRequired
	},
	getDefaultProps() {
		return {
			view: "list"
		};
	},
	render() {
		switch (this.props.view) {
			case "list":
				return <List stagiaires={this.props.stagiaires} onEdit={this.props.onEdit} />;
			case "edit":
				return <_Form stagiaire={this.props.stagiaire} evenements={this.props.evenements} onSave={this.props.onSave} />;
		}
	}
});

const List = React.createClass({
	getInitialState() {
		return {
			query: ""
		};
	},
	onEdit(stagiaire) {
		this.props.onEdit(stagiaire);
	},
	onQueryChange(event) {
		this.setState({
			query: event.target.value.toLowerCase()
		});
	},
	render() {
		const rows = this.props.stagiaires
			.filter(stagiaire => (stagiaire.prenom + " " + stagiaire.nom + " " + stagiaire.numeroId).toLowerCase().indexOf(this.state.query) > -1)
			.map(stagiaire => {
				return (
					<tr key={stagiaire.id} onClick={this.onEdit.bind(this, stagiaire)} className="clickable">
						<td>{stagiaire.nom}</td>
						<td>{stagiaire.prenom}</td>
						<td>{stagiaire.numeroId}</td>
						<td>
							<button type="button" onClick={this.onEdit.bind(this, stagiaire)} className="btn btn-default"><Glyph icon="eye-open" /> Dtails</button>
						</td>
					</tr>
				);
			});
		
		return (
			<div>
				<Layout.Row>
					<div className="form-inline">
						<div className="form-group spacingRight">
							<button type="button" onClick={this.onEdit.bind(this, create())} className="btn btn-primary"><Glyph icon="plus" /> Crer stagiaire</button>
						</div>
						<div className="form-group">
							<input onChange={this.onQueryChange} value={this.state.query} type="text" className="form-control" placeholder="Rechercher" />
						</div>
					</div>
				</Layout.Row>
				<Layout.Row>
					<Layout.Table cols={["Nom", "Prnom", "ID"]}>
						{rows}
					</Layout.Table>
				</Layout.Row>
			</div>
		);
	}
});

const _Form = React.createClass({
	mixins: [LinkedFormMixin],
	getInitialState: function () {
		return {
			linkedForm: $.extend({}, this.props.stagiaire)
		};
	},
	onSubmit: function (event) {
		event.preventDefault();
		this.props.onSave(this.state.linkedForm, {
			view: "list",
		});
	},
	onAddEvenement: function () {
		const evenements = this.state.linkedForm.evenements.concat(createStagiaireEvenement(this.refs.evenementSelect.value));
		this.setState({
			linkedForm: $.extend({}, this.state.linkedForm, { evenements })
		});
	},
	onDeleteEvenementClick: function (evenement) {
		const evenements = this.state.linkedForm.evenements.filter(e => e !== evenement);
		this.setState({
			linkedForm: $.extend({}, this.state.linkedForm, { evenements })
		});
	},
	onEvenementStatusChange: function (evenement, statut) {
		evenement.statut = statut;
		this.setState({
			linkedForm: this.state.linkedForm
		});
	},
	goToEvenement: function (evenement, event) {
		event.preventDefault();
		APP.edit("evenements", evenement.id);
	},
	render: function () {
		const _this = this;
		const stagiaireEvenementIds = this.state.linkedForm.evenements.map(evenement => evenement.evenementId);
		const evenements = this.props.evenements
			.filter(evenement => $.inArray(evenement.id, stagiaireEvenementIds) === -1)
			.map(evenement => {
				return (
					<option key={evenement.id} value={evenement.id}>
						{moment(evenement.date).format("DD/MM/YYYY")} {evenement.nom}
					</option>
				);
			});
		
		const stagiaireEvenements = this.state.linkedForm.evenements.map(stagiaireEvenement => {
			const evenement = this.props.evenements.filter(evenement => evenement.id === stagiaireEvenement.evenementId)[0];
			const labelType = stagiaireEvenement.statut === "PRESENT" ? "success" : (stagiaireEvenement.statut === "ABSENT" ? "warning" : "default");
			
			return (
				<tr key={"stagiaire-" + evenement.id}>
					<td><a href="#" onClick={_this.goToEvenement.bind(_this, evenement)}>{evenement.nom}</a></td>
					<td><Label text={stagiaireEvenement.statut} type={labelType} /></td>
					<td>
						<button onClick={this.onEvenementStatusChange.bind(this, stagiaireEvenement, "PRESENT")} type="button" className="btn btn-success" title="Prsent"><Glyph icon="ok" /> Prsent</button>
						{" "}
						<button onClick={this.onEvenementStatusChange.bind(this, stagiaireEvenement, "ABSENT")} type="button" className="btn btn-warning" title="Absent"><Glyph icon="remove" /> Absent</button>
						{" "}
						<button onClick={this.onDeleteEvenementClick.bind(this, stagiaireEvenement)} type="button" disabled={stagiaireEvenement.statut !== "INVITE"} className="btn btn-danger" title="Supprimer"><Glyph icon="trash" /> Supprimer</button>
					</td>
				</tr>
			);
		});
		
		return (
			<form onSubmit={this.onSubmit}>
				<Tabs defaultActiveKey={1}>
					<Tab eventKey={1} title="Identit">
						<Layout.Row spacing="Top" />
						<Form.Text name="nom" label="Nom" value={this.linkText("nom")} />
						<Form.Text name="prenom" label="Prnom" value={this.linkText("prenom")} />
						<Form.Text name="numeroId" label="Numro d'Identit" value={this.linkText("numeroId")} />
						<Form.Checkbox name="eligible" value={this.linkText("eligible")} label="Eligible" />
					</Tab>
					<Tab eventKey={2} title="Calendrier" disabled={!this.props.stagiaire.eligible}>
						<div className="row spacingTop">
							<Layout.Row size="col-md-8">
								<select className="form-control" ref="evenementSelect" disabled={evenements.length === 0}>{evenements}</select>
							</Layout.Row>
							<Layout.Row size="col-md-4">
								<button onClick={this.onAddEvenement} type="button" className="btn btn-primary" disabled={evenements.length === 0}><Glyph icon="plus" /> Ajouter vnement</button>
							</Layout.Row>
						</div>
						<div className="row">
							<Layout.Row>
								<Layout.Table cols={["Evnement", "Statut"]}>
									{stagiaireEvenements}
								</Layout.Table>
							</Layout.Row>
						</div>
					</Tab>
				</Tabs>
				<Form.Submit create={this.props.stagiaire.id === undefined} />
			</form>
		);
	}
});

export default Stagiaires;



