import $ from "jquery";
import moment from "moment";
import React, { PropTypes } from "react";
import { Panel } from "react-bootstrap/lib";
import MomentLocaleFR from "moment/locale/fr";
import DatePicker from "react-date-picker";
import APP from "./app";
import Stagiaires from "./stagiaires";
import classNames from "classnames";
import { Layout, Glyph, Form } from "./layout";
import { LinkedFormMixin } from "./mixins";

require("react-date-picker/base.css");
require("react-date-picker/theme/hackerone.css");

function create() {
	return {
		date: new Date(),
		nbStagiairesActuel: 0,
		nbStagiairesMin: 1,
		materiel: false
	};
}

const Evenements = {
	create
};

Evenements.View = React.createClass({
	propTypes: {
		view: PropTypes.string,
		evenements: PropTypes.arrayOf(PropTypes.object).isRequired,
		evenement: PropTypes.object,
		stagiaires: PropTypes.arrayOf(PropTypes.object).isRequired,
		formateurs: PropTypes.arrayOf(PropTypes.object).isRequired,
		salles: PropTypes.arrayOf(PropTypes.object).isRequired,
		onEdit: PropTypes.func.isRequired,
		onSave: PropTypes.func.isRequired
	},
	getDefaultProps() {
		return {
			view: "list"
		};
	},
	render: function () {
		switch (this.props.view) {
			case "list":
				return <List evenements={this.props.evenements} onEdit={this.props.onEdit} stagiaires={this.props.stagiaires} />;
			case "edit":
				return <_Form evenement={this.props.evenement} stagiaires={this.props.stagiaires} formateurs={this.props.formateurs} salles={this.props.salles} onSave={this.props.onSave} />;
		}
	}
});

Evenements.CalendrierItem = React.createClass({
	propTypes: {
		evenement: PropTypes.object.isRequired
	},
	onClick(event) {
		event.preventDefault();
		APP.edit("evenements", this.props.evenement.id);
	},
	render() {
		return (
			<Panel onClick={this.onClick} className="clickable">{moment(this.props.evenement.date).format("DD/MM/YYYY")} <a href="#">{this.props.evenement.nom}</a></Panel>
		);
	}
});


const _Form = React.createClass({
	mixins: [LinkedFormMixin],
	getInitialState() {
		return { showCalendar: false, linkedForm: $.extend({}, this.props.evenement) };
	},
	onCalendarToggle() { 
		this.setState({
			showCalendar: !this.state.showCalendar
		});
	},
	onDateChange(dateText, momentDate) {
		this.setState({
			linkedForm: $.extend(this.state.linkedForm, { date: momentDate.toDate() })
		});
	},
	onSubmit(event) {
		event.preventDefault();
		this.props.onSave(this.state.linkedForm);
	},
	render: function () {
		const newEvenement = this.props.evenement.id === undefined;
		const stagiairesMin = newEvenement ? false : (Stagiaires.countParticipants(this.props.stagiaires, this.props.evenement) >= this.props.evenement.nbStagiairesMin);
		const statut = stagiairesMin && this.props.evenement.materiel && this.props.evenement.formateurId && this.props.evenement.salleId ? "Ouvert" : "Propos";
		const minDate = moment.min(moment(this.state.linkedForm.date), moment());
		const cal = this.state.showCalendar ? <DatePicker date={moment(this.state.linkedForm.date)} onChange={this.onDateChange} minDate={minDate} dateFormat="DD/MM/YYYY" /> : null;
		const salles = this.props.salles;
		const optionMapper = function (salle) {
			return {
				value: salle.id,
				label: `${salle.nom} (${salle.places})`
			};
		};
		const formateurOptionMapper = formateur => {
			return {
				value: formateur.id,
				label: formateur.prenom + " " + formateur.nom
			};
		};
	
		return (
			<form onSubmit={this.onSubmit}>
				<div className="form-group">
					<label>Statut: {statut}</label>
					<div className="form-control-static">
						<span className={classNames({
							label: true,
							"label-default": !stagiairesMin,
							"label-success": stagiairesMin
						})}><Glyph icon={stagiairesMin ? "ok" : "remove"} /> Stagiaires</span>
						{" "}
						<span className={`label label-${this.props.evenement.salleId ? "success" : "default"}`}><Glyph icon={this.props.evenement.salleId ? "ok" : "remove"} /> Salle</span>
						{" "}
						<span className={`label label-${this.props.evenement.formateurId ? "success" : "default"}`}><Glyph icon={this.props.evenement.formateurId ? "ok" : "remove"} /> Formateur</span>
						{" "}
						<span className={`label label-${this.props.evenement.materiel ? "success" : "default"}`}><Glyph icon={this.props.evenement.materiel ? "ok" : "remove"} /> Matriel</span>
					</div>
				</div>
				<Form.Text name="nom" label="Nom" value={this.linkText("nom")} />
				<div className="form-group">
					<button onClick={this.onCalendarToggle} className="btn btn-default" type="button">
						<Glyph icon="calendar" />
						{" "}
						{moment(this.state.linkedForm.date).format("DD/MM/YYYY")}
					</button>
					<div className="row">
						<div className="col-md-4">
							{cal}
						</div>
					</div>
				</div>
				<div className="form-group">
					<label htmlFor="type">Type</label>
					<select id="type" className="form-control" valueLink={this.linkText("type")}>
						<option value="rdv">Rendez-vous individuel</option>
						<option value="reunion">Reunion d'information collective</option>
					</select>
				</div>
				<Form.Text name="nbStagiairesMin" label="Nombre minimum de stagiaires" value={this.linkNumber("nbStagiairesMin")} type="number" min={1} />
				<Form.Select name="salleId" value={this.linkNumber("salleId")} options={salles} optionMapper={optionMapper} label="Salle" />
				<Form.Select name="formateurId" value={this.linkNumber("formateurId")} options={this.props.formateurs} optionMapper={formateurOptionMapper} label="Formateur" />
				<Form.Checkbox label="Matriel disponible" checked={this.state.linkedForm.materiel} value={this.linkText("materiel")} />
				<Form.Submit create={newEvenement} />
			</form>
		);
	}
});

const List = React.createClass({
	onClick: function (evenement) {
		this.props.onEdit(evenement);
	},
	render: function () {
		const rows = this.props.evenements.map(evenement => {
			return (
				<tr onClick={this.onClick.bind(this, evenement)} key={evenement.id} className="clickable">
					<td>{evenement.nom}</td>
					<td>{moment(evenement.date).format("DD/MM/YYYY")}</td>
					<td>{Stagiaires.countParticipants(this.props.stagiaires, evenement)} / {evenement.nbStagiairesMin}</td>
					<td>
						<button type="button" className="btn btn-default"><Glyph icon="eye-open" /> Dtails</button>
					</td>
				</tr>
			);
		});
		
		return (
			<div>
				<Layout.Row>
					<button type="button" onClick={this.onClick.bind(this, create())} className="btn btn-primary"><Glyph icon="plus" /> Crer vnement</button>
				</Layout.Row>
				<Layout.Row spacing="Top">
					<Layout.Table cols={["Nom", "Date", "Nb Participants"]}>
						{rows}
					</Layout.Table>
				</Layout.Row>
			</div>
		);
	}
});

export default Evenements;



