import $ from "jquery";
import React, { PropTypes } from "react";
import LinkedStateMixin from "react-addons-linked-state-mixin";
import { Tab, Tabs } from "react-bootstrap/lib";
import Layout, { Glyph, Form } from "./layout";
import { CalendrierItem as EvenementCalendrierItem } from "./evenements";
import { LinkedNumberStateMixin } from "./mixins";

const Salles = {};

function create() {
	return {
		nom: "",
		places: 1
	};
}

function getCalendrier(salle, evenements) {
	return evenements.filter(ev => ev.salleId === salle.id);
}

Salles.View = React.createClass({
	propTypes: {
		salles: PropTypes.arrayOf(React.PropTypes.object).isRequired,
		evenements: PropTypes.arrayOf(React.PropTypes.object).isRequired,
		salle: PropTypes.object,
		view: PropTypes.string
	},
	getDefaultProps() {
		return {
			view: "list"
		};
	},
	render() {
		switch (this.props.view) {
			case "list":
				return <List salles={this.props.salles} onEdit={this.props.onEdit} />;
			case "edit":
				return <Item salle={this.props.salle} evenements={this.props.evenements} onSave={this.props.onSave} />
		}
	}
});

const List = React.createClass({
	propTypes: {
		salles: PropTypes.array.isRequired
	},
	onSalleClick(salle) {
		this.props.onEdit(salle);
	},
	render() {
		const rows = this.props.salles.map(salle => {
			return (
				<tr key={salle.id} onClick={this.onSalleClick.bind(this, salle)} className="clickable">
					<td>{salle.nom}</td>
					<td>{salle.places}</td>
					<td>
						&nbsp;
					</td>
				</tr>
			);
		});
	
		return (
			<div>
				<Layout.Row>
					<button onClick={this.onSalleClick.bind(this, create())} type="button" className="btn btn-primary">
						<Glyph icon="plus"/> Crer salle
					</button>
				</Layout.Row>
				<Layout.Row spacing="Top">
					<Layout.Table cols={["Nom", "Places"]}>
						{rows}
					</Layout.Table>
				</Layout.Row>
			</div>
		);
	}
});

const Item = React.createClass({
	mixins: [LinkedStateMixin, LinkedNumberStateMixin],
	propTypes: {
		salle: PropTypes.object.isRequired,
		evenements: PropTypes.arrayOf(PropTypes.object).isRequired
	},
	getInitialState() {
		return $.extend({}, this.props.salle);
	},
	onSubmit(event) {
		event.preventDefault();
		this.props.onSave(this.state);
	},
	render() {
		const salleEvenements = getCalendrier(this.props.salle, this.props.evenements).map(evenement => {
			return <EvenementCalendrierItem key={evenement.id} evenement={evenement} />
		});
	
		return (
			<form onSubmit={this.onSubmit}>
				<Tabs defaultActiveKey={1}>
					<Tab eventKey={1} title="Info">
						<div className="spacingTop">
							<Form.Text name="nom" label="Nom" value={this.linkState("nom")} />
							<Form.Text name="places" value={this.linkNumberState("places")} type="number" min={1} label="Places" />
						</div>
					</Tab>
					<Tab eventKey={2} title="Calendrier" disabled={this.props.salle.id === undefined}>
						<div className="spacingTop">
							{salleEvenements}
						</div>
					</Tab>
				</Tabs>
				<Form.Submit create={this.props.salle.id === undefined} />
			</form>
		);
	}
});

export default Salles;



