import React from "react";
import APP from "./app";
import storage from "./storage";
import Login from "./login";
import Dashboard from "./dashboard";
import Individus from "./individus";
import Stagiaires from "./stagiaires";
import Evenements from "./evenements";
import Salles from "./salles";
import Formateurs from "./formateurs";
import Layout from "./layout";

const Index = React.createClass({
	propTypes: {
		view: React.PropTypes.string.isRequired,
		data: React.PropTypes.object.isRequired
	},
	onEdit(tableName, record) {
		APP.render(tableName, {
			view: "edit",
			record
		});
	},
	onSave(tableName, record, viewData) {
		if (record.id !== undefined) {
			storage.set(tableName, record);
		} else {
			storage.create(tableName, record);
		}
		
		APP.render(tableName, viewData);
	},
	render() {
		if (!APP.logged || this.props.view === "login") {
			return <Login />
		}
		
		let body;
		const data = this.props.data;

		if (APP.role === "Individu") {
			body = <Individus.View />;
		} else {
			const stagiaires = storage.get("stagiaires");
			const salles = storage.get("salles");
			const formateurs = storage.get("formateurs");
			const evenements = storage.get("evenements");
			
			if (APP.view === "dashboard") {
				body = <Dashboard />;
			} else if (this.props.view === "stagiaires") {
				body = <Stagiaires.View onEdit={this.onEdit.bind(this, "stagiaires")} onSave={this.onSave.bind(this, "stagiaires")} view={data.view} stagiaires={stagiaires} stagiaire={data.record} evenements={evenements} />
				delete APP.viewData; 
			} else if (this.props.view === "evenements") {
				body = <Evenements.View onEdit={this.onEdit.bind(this, "evenements")} onSave={this.onSave.bind(this, "evenements")} view={data.view} evenement={data.record} evenements={evenements} stagiaires={stagiaires} formateurs={formateurs} salles={salles} />
			} else if (this.props.view === "salles") {
				body = <Salles.View onEdit={this.onEdit.bind(this, "salles")} onSave={this.onSave.bind(this, "salles")} view={data.view} salle={data.record} salles={salles} evenements={evenements} />
			} else if (this.props.view === "formateurs") {
				body = <Formateurs.View onEdit={this.onEdit.bind(this, "formateurs")} onSave={this.onSave.bind(this, "formateurs")} view={data.view} formateurs={formateurs} formateur={data.record} evenements={evenements} />
			}
		}
		
		return (
			<Layout.Global>
				<Layout.Navbar />
				{body}
			</Layout.Global>
		);
	}
});

export default Index;


