import $ from "jquery";
import React from "react";
import ReactDOM from "react-dom";
import Index from "./index";
import storage, { StorageEvent } from "./storage";

export default {
	logged: false,
	render(view, optionalViewData) {
		this.view = view;
		this.viewData = optionalViewData;
		ReactDOM.render(React.createElement(Index, {
			view,
			data: optionalViewData || {}
		}), document.getElementById("app"));
	},
	edit(view, recordId) {
		this.render(view, {
			view: "edit",
			record: storage.get(view, recordId)
		});
	} 
};

