import moment from "moment";
import React from "react";
import { Layout, Glyph } from "./layout";
import APP from "./app";
import storage from "./storage";
import Evenements from "./evenements";
import Stagiaires from "./stagiaires";

const Dashboard = React.createClass({
	onClick(view, record) {
		APP.render(view, {
			view: "edit",
			record
		});
	},
	render() {
		const events = storage.get("events");
		events.sort((event1, event2) => {
			if (moment(event1.date).isBefore(event2.date)) {
				return 1;
			} else if (moment(event1.date).isAfter(event2.date)) {
				return -1;
			} else {
				return 0;
			}
		});
		const eventRows = events.map(event => {
			return <Event key={event.getKey()} event={event} />;
		});
	
		return (
			<div>
				<Layout.Row>
					<h1>Dashboard</h1>
				</Layout.Row>
				<Layout.Row spacing="Top">
					<button onClick={this.onClick.bind(this, "stagiaires", Stagiaires.create())} type="button" className="btn btn-default btn-lg"><Glyph icon="user" /> Crer stagiaire</button>
					{" "}
					<button onClick={this.onClick.bind(this, "evenements", Evenements.create())} type="button" className="btn btn-default btn-lg"><Glyph icon="calendar" /> Crer vnement</button>
				</Layout.Row>
				<Layout.Row spacing="Top">
					{eventRows}
				</Layout.Row>
			</div>
		);
	}
});

const Event = React.createClass({
	render() {
		const tableName = this.props.event.type.split("-")[0];
		const record = storage.get(tableName, this.props.event.payload);
		
		switch (this.props.event.type) {
			case "stagiaires-create":
				return <Event.View tableName={tableName} record={record} text={`Le stagiaire ${record.prenom} ${record.nom} a t cr`} />;
			case "evenements-create":
				return <Event.View tableName={tableName} record={record} text={`L'vnement ${record.nom} a t programm pour le ${moment(record.date).format("DD/MM/YYYY")}`} />;
			case "salles-create":
				return <Event.View tableName={tableName} record={record} text={`La salle ${record.nom} a t cre`} />;
			case "formateurs-create":
				return <Event.View tableName={tableName} record={record} text={`Le formateur ${record.nom} a t cr`} />;
			default:
				return <div>UNKNOWN EVENT: {this.props.event.type}</div>
		}
	}
});

Event.View = React.createClass({
	displayName: "Event.View",
	propTypes: {
		tableName: React.PropTypes.string.isRequired,
		record: React.PropTypes.object.isRequired,
		text: React.PropTypes.string.isRequired
	},
	onClick() {
		APP.edit(this.props.tableName, this.props.record.id);
	},
	render() {
		return (
			<div onClick={this.onClick} className="spacingTop clickable" style={{border: "solid #ddd 1px", padding: "1em" }}>
				{this.props.text}
			</div>
		);
	}
});

Event.Stagiaires = {
	Create: React.createClass({
		propTypes: {
			payload: React.PropTypes.number.isRequired
		},
		getInitialState() {
			return {
				stagiaire: storage.get("stagiaires", this.props.payload)
			};
		},
		onClick() {
			APP.render("stagiaires", this.state.stagiaire);
		},
		render() {
			const stagiaire = this.state.stagiaire;
			return (
				<div onClick={this.onClick} className="spacingTop clickable" style={{border: "solid #ddd 1px", padding: "1em" }}>
					Le stagiaire {stagiaire.prenom} {stagiaire.nom} a t cr
				</div>
			);
		}
	})
};

Event.Evenements = {
	Create: React.createClass({
		propTypes: {
			payload: React.PropTypes.number.isRequired
		},
		getInitialState() {
			return {
				evenement: storage.get("evenements", this.props.payload)
			};
		},
		onClick() {
			APP.render("evenements", this.state.evenement);
		},
		render() {
			const evenement = this.state.evenement;
			return (
				<div onClick={this.onClick} className="spacingTop clickable" style={{border: "solid #ddd 1px", padding: "1em" }}>
					L'vnement {evenement.nom} a t programm pour le {moment(evenement.date).format("DD/MM/YYYY")}
				</div>
			);
		}
	})
};

Event.Salles = {
	Create: React.createClass({
		propTypes: {
			payload: React.PropTypes.number.isRequired
		},
		render() {
			const salle = storage.get("salles", this.props.payload);
			return (
				<div onClick={this.onClick} className="spacingTop clickable" style={{border: "solid #ddd 1px", padding: "1em" }}>
					La salle {salle.nom} a t cre
				</div>
			);
			
		}
	})
};

Event.Formateurs = {
	Create: React.createClass({
		propTypes: {
			payload: React.PropTypes.number.isRequired
		},
		render() {
			const formateur = storage.get("formateurs", this.props.payload);
			return (
				<div onClick={this.onClick} className="spacingTop clickable" style={{border: "solid #ddd 1px", padding: "1em" }}>
					Le formateur {formateur.nom} a t cre
				</div>
			);
			
		}
	})
};

export default Dashboard;



