import $ from "jquery";
import React, { PropTypes } from "react";
import LinkedStateMixin from "react-addons-linked-state-mixin";
import { Tabs, Tab, Panel } from "react-bootstrap/lib";
import Layout, { Glyph, Form } from "./layout";
import Evenements from "./evenements";
import { LinkedNumberStateMixin } from "./mixins";
import storage from "./storage";
import APP from "./app";

const Formateurs = {};

function create() {
	return {
		nom: "",
		prenom: ""
	};
}

function getCalendrier(formateur, evenements) {
	return evenements.filter(ev => ev.formateurId === formateur.id);
}

Formateurs.View = React.createClass({
	getDefaultProps() {
		return {
			view: "list"
		};
	},
	render() {
		switch (this.props.view) {
			case "list":
				return <List formateurs={this.props.formateurs} onEdit={this.props.onEdit} />;
			case "edit":
				return <Item formateur={this.props.formateur} onSave={this.props.onSave} evenements={this.props.evenements} />
		}
	}
});

const List = React.createClass({
	propTypes: {
		formateurs: React.PropTypes.array.isRequired
	},
	onClick(formateur) {
		this.props.onEdit(formateur);
	},
	render() {
		const rows = this.props.formateurs.map(formateur => {
			return (
				<tr key={formateur.id} onClick={this.onClick.bind(this, formateur)} className="clickable">
					<td>{formateur.prenom} {formateur.nom}</td>
					<td>
						&nbsp;
					</td>
				</tr>
			);
		});
	
		return (
			<div>
				<Layout.Row>
					<button onClick={this.onClick.bind(this, create())} type="button" className="btn btn-primary">
						<Glyph icon="plus"/> Crer Formateur
					</button>
				</Layout.Row>
				<Layout.Row spacing="Top">
					<Layout.Table cols={["Nom"]}>
						{rows}
					</Layout.Table>
				</Layout.Row>
			</div>
		);
	}
});

const Item = React.createClass({
	mixins: [LinkedStateMixin, LinkedNumberStateMixin],
	propTypes: {
		formateur: PropTypes.object.isRequired,
		evenements: PropTypes.arrayOf(PropTypes.object).isRequired
	},
	getInitialState() {
		return $.extend({}, this.props.formateur);
	},
	onSubmit(event) {
		event.preventDefault();
		this.props.onSave(this.state);
	},
	render() {
		const evenementPanels = this.props.evenements.map(evenement => <Evenements.CalendrierItem key={evenement.id} evenement={evenement} />);
		
		return (
			<Tabs defaultActiveKey={1}>
				<Tab eventKey={1} title="Info">
					<form onSubmit={this.onSubmit} className="spacingTop">
						<Form.Text name="nom" label="Nom" value={this.linkState("nom")} />
						<Form.Text name="prenom" label="Prnom" value={this.linkState("prenom")} />
						<Form.Submit create={this.props.formateur.id === undefined} />
					</form>
				</Tab>
				<Tab eventKey={2} title="Calendrier" disabled={this.props.formateur.id === undefined}>
					<div className="spacingTop">
						{evenementPanels}
					</div>
				</Tab>
			</Tabs>
		);
	}
});

export default Formateurs;



