import React from "react";
import LinkedStateMixin from "react-addons-linked-state-mixin";
import APP from "./app";
import storage from "./storage";
import Stagiaires from "./stagiaires";
import { Glyph, Form } from "./layout";

const Individus = {};

(function () {
	Individus.View = React.createClass({
		getInitialState: function () {
			return {
				view: "form"
			};
		},
		onSubmit: function (stagiaire) {
			storage.create("stagiaires", stagiaire);
			this.setState({
				view: "thankYou"
			});
		},
		render: function () {
			switch (this.state.view) {
				case "form":
					return <_Form onSubmit={this.onSubmit} />
				case "thankYou":
					return <_ThankYou />
			}
		}
	});

	const _Form = React.createClass({
		mixins: [LinkedStateMixin],
		propTypes: {
			onSubmit: React.PropTypes.func.isRequired
		},
		getInitialState: Stagiaires.create,
		onSubmit: function (event) {
			event.preventDefault();
			this.props.onSubmit(this.state);
		},
		render: function () {
			return (
				<form onSubmit={this.onSubmit}>
					<h2>Formulaire d'inscription</h2>
					<Form.Text name="nom" label="Nom" value={this.linkState("nom")} />
					<Form.Text name="prenom" label="Prnom" value={this.linkState("prenom")} />
					<Form.Text name="numeroId" label="Numro ID" value={this.linkState("numeroId")} />
					<button type="submit" className="btn btn-primary"><Glyph icon="ok" /> Soumettre</button>
				</form>
			);
		}
	});
	
	const _ThankYou = React.createClass({
		render: function () {
			return (
				<div className="alert alert-success" role="alert">
					Votre demande a bien t prise en compte.
				</div>
			);
		}
	});
}());

export default Individus;



